.HeroHome {
  min-height: calc(90vmin);
  background-image: url("https://www.pngall.com/wp-content/uploads/2/Mario-PNG-Clipart.png");
  background-position: right top;
  background-repeat: no-repeat;
  background-size: 50%;
}

.HeroHome-container {
  width: 40%;
  padding: 10%;
}

.HeroHome-container p {
  color: #555;
  margin: 5% 0;
  width: 80%;
  text-align: justify;
}

@media screen and (max-width: 480px) {
  .HeroHome {
    min-height: calc(90vmin);
    background-image: url("https://www.pngall.com/wp-content/uploads/2/Mario-PNG-Clipart.png");
    background-position: right top;
    background-repeat: no-repeat;
    background-size: 100%;
  }
  
  .HeroHome-container {
    width: 80%;
    padding: 80% 10% 10% 10%;
  }
  
  .HeroHome-container p {
    color: #555;
    margin: 5% 0;
    width: 100%;
    text-align: justify;
  }
}

@media screen and (max-width: 768px) and (min-width: 481px) {
  .HeroHome {
    min-height: calc(60vmin);
  }
}