.App {
  text-align: center;
  background-image: url("https://assets-prd.ignimgs.com/2021/11/02/image-2021-11-02-121222-1635855143077.png");
  background-position: center;
  background-repeat: repeat;
  background-size: cover;
  border-top: 1px solid #000;
  padding: 20px 0;
  color: #FFF;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  justify-content: center;
  color: black;
  margin-bottom: 50px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-grid-container {
  display: grid;
  gap: 20px;
  grid-template-columns: 20% 20% 20% 20%;
  padding: 10px;
  justify-content: center;
  margin: 10px 0;
}

.App-grid-item {
  border: 5px solid rgba(0, 0, 0, 0.8);
  padding: 20px;
  font-size: 1rem;
  text-align: left;
  border-radius: 10px;
  box-shadow: 5px 5px 10px 3px #333;
  cursor: pointer;
  background-color: #FFF;
  position: relative;
}

.App-grid-item:hover {
  box-shadow: none;
}

.Close-icon, .Lock-icon {
  position: absolute;
  top: 5px; 
  right: 5px; 
}

.Close-icon .icon {
  width: 1.5em;
  height: 1.5em;
  color: red;
}

.Lock-icon .icon {
  width: 1.5em;
  height: 1.5em;
}

/* Mobile version */
@media screen and (max-width: 480px) {
  .App-grid-container {
    display: grid;
    gap: 20px;
    grid-template-columns: auto;
    padding: 10px;
    justify-content: center;
    margin: 10px 20px;
  }
  
  .App-grid-item {
    border: 5px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 1rem;
    text-align: left;
    border-radius: 10px;
    box-shadow: 5px 5px 10px 3px gray;
    cursor: pointer;
  }
}

@media screen and (max-width: 768px) and (min-width: 481px) {
  .App-grid-container {
    display: grid;
    gap: 20px;
    grid-template-columns: 40% 40%;
    padding: 10px;
    justify-content: center;
    margin: 10px 20px;
  }
  
  .App-grid-item {
    border: 5px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 1rem;
    text-align: left;
    border-radius: 10px;
    box-shadow: 5px 5px 10px 3px gray;
    cursor: pointer;
  }
}
