.Footer {
  margin: 20px 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Footer-logo {
  height: 10vmin;
}

.Footer-menu {
  list-style-type: none;
  display: flex;
}

.Footer-start {
  display: flex;
  align-items: center;
}

.Footer-menu-link {
  text-decoration: none;
  color: #000;
  font-size: 1.5rem;
}

.Footer-menu-link:hover {
  color: #555;
}

.Footer-menu li:nth-child(odd) {
  padding-right: 50px;
}

.Footer-menu li:nth-child(2) {
  padding-right: 50px;
}

@media screen and (max-width: 480px) {
  .Footer {
    margin: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .Footer-menu li:nth-child(odd) {
    padding-right: 20px;
  }
  
  .Footer-menu li:nth-child(even) {
    padding-right: 20px;
  }
}