.Header {
  margin: 20px 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Header-logo {
  height: 10vmin;
  pointer-events: none;
}

.Header-menu {
  display: flex;
  list-style-type: none;
}

.Header-menu-link {
  text-decoration: none;
  color: black;
}

.Header-menu-link:hover {
  color: gray;
}

.Header-menu-button {
  border: 1px solid #a15df4;
  padding: 10px 20px;
  text-decoration: none;
  color: #FFF;
  margin-left: 20px;
  border-radius: 10px;
  background-color:  #a15df4;
}

.Header-menu-button:hover {
  background:  #9a50f5;
  border: 1px solid #9a50f5;
}

