.Form-input {
  padding: 0.5rem 0;
  font-size: 1rem;
  border: 0;
  border-bottom: 1px solid black;
  background-color: transparent;
  width: 100%;
}

.Form-input:focus {
  outline: none;
}

.Form-form {
  width: 70vmin;
}

.Form-form fieldset {
  border-radius: 10px;
  border: 2px solid black;
}

.Form-form fieldset legend {
  font-size: 1.5rem;
}

.Form-form-control {
  margin: 10px;
  text-align: left;
  font-size: 1rem;
}

.Form-form-button {
  margin-top: 20px;
  margin-bottom: 10px;
  text-align: right;
}

.Form-form small {
  color: red;
}

@media screen and (max-width: 768px) and (min-width: 481px) {
  .Form-form {
    width: 40vmin;
  }
}

@media screen and (max-width: 480px) {
  .Form-form {
    margin: 0 auto;
    width: 80vmin;
  }
}