.AddGame {
  min-height: calc(100vmin);
  background-image: url("https://i.pinimg.com/originals/ac/22/59/ac225995e118a67fd7ce3cecc43fc81a.png");
  background-position: left top;
  background-repeat: no-repeat;
  background-size: 60%;
  border-top: 1px solid black;
}

.AddGame-container {
  width: 35%;
  padding: 8% 10%;
  float: right;
}

.AddGame-checked {
  padding: 20% 0 10% 30%;
}

.AddGame-thanks {
  padding: 20% 0 10% 30%;
}

.AddGame-thanks p {
  padding: 20px;
  color: green;
  border-left: 5px solid green;
}

.AddGame-checked-logo {
}

@media screen and (max-width: 480px) {
  .AddGame {
    background-image: none;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 0%;
    border-top: 0px solid black;
    max-height: max-content;
    margin-bottom: 20%;
    width: 35%;
    padding: 10% 13%;
    float: none;
  }

  .AddGame-container {
    width: 100%;
  }

  .AddGame-checked {
    padding: 20% 0 10% 30%;
  }
  
  .AddGame-thanks {
    padding: 20% 0 10% 30%;
    width: 100%;
  }
  
  .AddGame-thanks p {
    background-color: transparent;
    width: 100%;
    padding: 20px;
    border-left: 5px solid green;
  }
}

@media screen and (max-width: 768px) and (min-width: 481px) {
  .AddGame {
    min-height: 70vmin;
  }

  .AddGame-container {
    width: 35%;
    padding: 8% 10%;
    float: right;
  }
}
